h2 {
  font-size: 2.5rem;
  line-height: 130%;
  font-weight: 700;
  text-transform: uppercase;
  margin: 0;
  .text_color {
    color: rgb(22, 123, 242);
  }
}
.btn-blue {
  background: rgb(22, 123, 242);
  border: 0.0625rem solid rgb(22, 123, 242) !important;
  &:hover {
    background: rgb(5, 103, 218);
    border-color: rgb(5, 103, 218) !important;
  }
  &:active {
    background: rgb(6, 83, 173);
    border-color: rgb(6, 83, 173) !important;
  }
  span {
    font-size: 1rem;
    line-height: 130%;
    font-weight: 700;
    text-transform: uppercase;
    color: rgb(255, 255, 255);
    padding: 1.25rem 1.875rem;
  }
}
.btn-wb {
  background: rgb(139, 29, 139);
  padding: 0 0.5rem !important;
  clip-path: polygon( 3.038% 11.418%,6.193% 0%,100% 0%,100% 75%,96.674% 87.5%,93.349% 100%,0% 100%,0% 22.414%,3.038% 11.418% );
  &:hover {
    background: rgb(182, 56, 182);
  }
  &:active {
    background: rgb(118, 6, 118);
  }
  span {
    font-size: 1rem;
    line-height: 130%;
    font-weight: 700;
    text-transform: uppercase;
    color: rgb(255, 255, 255);
    padding: 0;
  }
  i {
    width: 3.75rem;
    img {
      width: 100%;
    }
  }
}
.btn-outline {
  background: transparent;
  border: 0.0625rem solid rgb(22, 123, 242) !important;
  &:hover {
    background: rgb(22, 123, 242);
    span {
      color: #ffffff;
    }
  }
  &:active {
    background: rgb(6, 83, 173);
    border-color: rgb(6, 83, 173) !important;
    span {
      color: #ffffff;
    }
  }
  span {
    font-size: 1rem;
    line-height: 130%;
    font-weight: 700;
    text-transform: uppercase;
    color: rgb(22, 123, 242);
    padding: 1.25rem 1.875rem;
  }
}
.form-group {
  border: none;
  padding: 0;
  margin: 0;
  input {
    height: 3.625rem;
    width: 100%;
    background: rgb(237, 236, 236);
    border-color: transparent;
    padding: 0 1.25rem;
    font-size: 1rem;
    line-height: 130%;
    color: rgb(22, 22, 22);
    &::placeholder {
      color: rgb(137, 132, 132) !important;
    }
  }
}
.left-anim {
  transform: translate(-60%, 0);
  opacity: 0;
  transition: all 0.6s;
}
.left-anim.active {
  transform: translate(0,0);
  opacity: 1;
}
.right-anim {
  transform: translate(60%, 0);
  opacity: 0;
  transition: all 0.6s;
}
.right-anim.active {
  transform: translate(0,0);
  opacity: 1;
}

@media screen and (max-width: 1300px) {
  h2 {
    font-size: 2rem;
  }
}

@media screen and (max-width: 1023px) {
  h2 {
    font-size: 1.75rem;
  }
  .btn-blue {
    span {
      font-size: 0.875rem;
    }
  }
  .btn-wb {
    span {
      font-size: 0.875rem;
    }
  }
  .btn-outline {
    span {
      font-size: 0.875rem;
    }
  }
  .form-group {
    input {
      font-size: 0.875rem;
    }
  }
}

@media screen and (max-width: 576px) {
  h2 {
    font-size: 1.5rem;
  }
}