#header {
  padding: 0;
  .header-top__socials {
    display: flex;
    align-items: center;
    gap: 0.875rem;
    margin-left: 6.25rem;
  }
  .header-top__social {
    width: 2.25rem;
    height: 2.25rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgb(22, 123, 242);
    border-radius: 50%;
    padding: 0.3125rem;
    &:hover {
      background: rgb(5, 103, 218);
    }
    &:active {
      background: rgb(6, 83, 173);
    }
  }
}
#header-top {
  .header-top__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
    border-bottom: 0.0625rem solid rgb(174, 174, 174);
    padding: 1.4375rem 0 2.125rem;
  }
  .header-top__left {
    display: flex;
    align-items: center;
    gap: 0.9375rem;
  }
  .header-top__right {
    display: flex;
    align-items: center;
  }
  .header-top__logo {
    width: 5.8125rem;
    display: block;
    img {
      width: 100%;
    }
  }
  .header-top__text {
    font-size: 1rem;
    line-height: 130%;
  }
  .header-top__group {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-left: 2rem;
  }
  .header-top__phone {
    font-size: 1.25rem;
    line-height: 130%;
    font-weight: 700;
    &:hover {
      color: rgb(22, 123, 242);
    }
  }
  .header-top__btn {
    position: relative;
    background: transparent;
    padding-left: 1.125rem;
    margin-top: 0.1875rem;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(0, -50%);
      width: 0.6875rem;
      height: 0.6875rem;
      border: 0.1875rem solid rgb(22, 123, 242);
      border-radius: 50%;
    }
    &:hover {
      color: rgb(22, 123, 242);
      span {
        border-color: rgb(22, 123, 242);
      }
    }
    span {
      font-size: 1rem;
      line-height: 130%;
      padding: 0;
      border-bottom: 0.0625rem dashed rgb(22, 22, 22);
      padding-bottom: 0.125rem;
    }
  }
  .menu {
    display: none;
  }
  .header-mob {
    display: none;
    .search-btn {
      height: 2.5rem;
      border-radius: 50%;
    }
  }
}
#header-bottom {
  padding: 1.5rem 0 0 0;
  .menu {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1.5rem;
  }
  .header-top__socials {
    display: none;
  }
  .header-top__group {
    display: none;
  }
}
.search {
  position: relative;
}
.search-show {
  height: 2.5rem;
  display: flex;
  align-items: stretch;
  input {
    max-width: 11.75rem;
    width: 100%;
    height: 100%;
    font-size: 1rem;
    line-height: 130%;
    background: rgb(237, 236, 236);
    border-color: transparent;
    padding: 0 1rem;
    &::placeholder {
      color: rgb(137, 132, 132) !important;
    }
  }
}
.search-btn {
  width: 3.625rem;
  height: 100%;
  flex-shrink: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgb(22, 123, 242);
  cursor: pointer;
  img {
    width: 1.125rem;
  }
}
.search-result {
  position: absolute;
  z-index: 3;
  top: calc(100% + 0.75rem);
  left: 0;
  width: 100%;
  background: rgb(255, 255, 255);
  border: 0.0625rem solid rgb(22, 123, 242);
  padding: 1.25rem 1rem;
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s;
  &--active {
    opacity: 1;
    pointer-events: auto;
  }
  ul {
    display: grid;
    grid-template-columns: 100%;
    gap: 0.625rem;
  }
  ul li {
    font-size: 1rem;
    line-height: 130%;
    &:hover {
      color: rgb(22, 123, 242);
    }
    a {
      display: block;
    }
  }
}
.search-mob {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgb(22, 123, 242);
  padding: 0 1rem;
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s;
  &--active {
    opacity: 1;
    pointer-events: auto;
  }
  .search-mob__wrap {
    position: relative;
    max-width: 25rem;
    width: 100%;
  }
  .search-show {
    height: 3.75rem;
    justify-content: center;
  }
  input {
    max-width: 100%;
  }
  .search-btn {
    width: 3.75rem;
    background: rgb(22, 22, 22);
  }
  .search-close {
    width: 1.75rem;
    height: 1.75rem;
    visibility: unset;
    svg {
      width: 100%;
      height: auto;
    }
    svg path {
      fill: #ffffff;
    }
  }
}
.menu {
  .menu-item {
    &:after,
    &:before {
      display: none;
    }
    a {
      font-size: 1.125rem;
      line-height: 130%;
    }
  }
  .menu-item--active {
    font-weight: 700;
    color: rgb(22, 123, 242);
  }
  .menu-item a {
    display: flex;
    align-items: center;
    border-left: unset;
    padding: 0;
    &:hover {
      color: rgb(22, 123, 242);
      svg path {
        fill: rgb(22, 123, 242);
      }
    }
  }
  .menu-item a i {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 0.3125rem;
  }
  .menu-item a i svg {
    transition: all 0.3s;
  }
  .sub-menu {
    display: grid;
    grid-template-columns: 100%;
    gap: 0.75rem;
    a {
      padding: 0 !important;
    }
    a:hover {
      color: rgb(22, 123, 242) !important;
    }
  }
}
.burger {
  position: relative;
  z-index: 12;
  width: 2rem;
  height: 1.25rem;
  cursor: pointer;
  span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 0.1875rem;
    background: rgb(22, 22, 22);
    border-radius: 0.1875rem;
    transition: all 0.5s;
  }
  span:nth-child(1) {
    top: 0;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
  }
  span:nth-child(3) {
    bottom: 0;
  }
  &--active {
    span {
      background: #ffffff;
    }
    span:nth-child(1) {
      top: 0.5rem;
      transform: rotate(-45deg);
    }
    span:nth-child(2) {
      display: none;
    }
    span:nth-child(3) {
      bottom: 0.5rem;
      transform: rotate(45deg);
    }
  }
}

@media screen and (max-width: 1300px) {
  #header-top {
    .header-top__inner {
      gap: 1.5rem;
    }
    .header-top__text {
      font-size: 0.875rem;
    }
    .header-top__socials {
      gap: 0.625rem;
      margin-left: 1.5rem;
    }
    .header-top__group {
      margin-left: 1.5rem;
    }
  }
}

@media screen and (max-width: 1180px) {
  #header-top {
    .search-show {
      height: 2.375rem;
      input {
        max-width: 10rem;
      }
    }
    .search-btn {
      width: 2.5rem;
    }
    .header-top__social {
      width: 2rem;
      height: 2rem;
      img {
        max-width: 1.125rem;
      }
    }
    .header-top__phone {
      font-size: 1.125rem;
    }
  }
  .menu {
    .menu-item {
      a {
        font-size: 1rem;
      }
    }
    .menu-item--active {
      font-weight: 700;
      color: rgb(22, 123, 242);
    }
    .menu-item a {
      display: flex;
      align-items: center;
      border-left: unset;
      padding: 0;
      &:hover {
        color: rgb(22, 123, 242);
        svg path {
          fill: rgb(22, 123, 242);
        }
      }
    }
    .menu-item a i {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-left: 0.3125rem;
    }
    .menu-item a i svg {
      transition: all 0.3s;
    }
    .sub-menu {
      display: grid;
      grid-template-columns: 100%;
      gap: 0.75rem;
      a {
        padding: 0 !important;
      }
      a:hover {
        color: rgb(22, 123, 242) !important;
      }
    }
  }
}

@media screen and (max-width: 1023px) {
  #header-top {
    z-index: 10;
    .header-top__inner {
      border: none;
      padding: 1rem 0;
    }
    .header-top__right {
      position: fixed;
      z-index: 10;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      flex-direction: column;
      gap: 1.5rem;
      background: rgb(22, 123, 242);
      padding: 6.25rem 1.5rem 5rem;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
      overflow-y: auto;
      &--active {
        opacity: 1;
        pointer-events: auto;
      }
      .search {
        display: none;
      }
      .header-top__phone {
        color: #ffffff;
        &:hover {
          opacity: 0.7;
        }
      }
      .header-top__btn {
        color: #ffffff;
        &:after {
          border-color: #ffffff;
        }
        &:hover {
          opacity: 0.7;
        }
        span {
          border-color: #ffffff;
        }
      }
    }
    .header-top__socials {
      order: 1;
    }
    .header-top__social {
      background: rgb(22, 22, 22);
      &:hover,
      &:active {
        background: rgb(54, 54, 54);
      }
    }
    .header-mob {
      display: flex;
      align-items: center;
      gap: 1.5rem;
    }
    .header-top__group {
      margin-left: 0;
    }
  }
  #header-bottom {
    display: none;
  }
  .menu {
    display: flex !important;
    flex-direction: column !important;
    text-align: center;
    gap: 1.5rem;
    .menu-item {
      color: #ffffff;
    }
    .menu-item a {
      justify-content: center;
      &:hover {
        color: #ffffff;
        opacity: 0.7;
      }
    }
    .menu-item i,
    .sub-menu {
      display: none !important;
    }
  }
}

@media screen and (max-width: 768px) {
  #header-top {
    .header-top__text {
      font-size: 0.75rem;
    }
    .header-top__phone {
      font-size: 1rem;
    }
    .header-top__btn {
      span {
        font-size: 0.875rem;
      }
    }
    .header-mob {
      gap: 1.25rem;
    }
  }
}

@media screen and (max-width: 576px) {
  #header-top {
    .header-top__text {
      display: none;
    }
  }
}

@media screen and (max-width: 399px) {
  #header-top {
    .header-mob {
      .header-top__group {
        display: none;
      }
    }
  }
}