.main-section {
  padding: 7rem 0 9.375rem;
  &__desc {
    position: relative;
    z-index: 1;
  }
  h1 {
    font-size: 3.625rem;
    line-height: 110%;
    font-weight: 800;
    text-transform: uppercase;
    color: rgb(21, 21, 21);
    margin: 0;
    .text_color {
      font-size: 2.3125rem;
      color: rgb(22, 123, 242);
    }
    em {
      font-style: normal;
      font-size: 2.875rem;
    }
  }
  p {
    font-size: 1.5rem;
    line-height: 130%;
    font-weight: 700;
    margin: 1.5rem 0 0 0;
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 1.5rem;
    margin-top: 3.5rem;
  }
  &__btn {
    flex-shrink: 0;
    align-items: stretch !important;
    gap: 0.3125rem;
    span {
      font-size: 1rem;
      line-height: 130%;
      text-transform: uppercase;
      color: rgb(255, 255, 255);
      background: rgb(22, 123, 242);
      padding: 1.25rem 2rem;
      transition: all 0.3s;
    }
    i {
      width: 3.75rem;
      display: flex;
      justify-content: center;
      align-items: center;
      background: rgb(22, 123, 242);
      transition: all 0.3s;
      img {
        max-width: 1.875rem;
      }
    }
    &:hover {
      span {
        background: rgb(5, 103, 218);
      }
      i {
        background: rgb(5, 103, 218);
      }
    }
    &:active {
      span {
        background: rgb(6, 83, 173);
      }
      i {
        background: rgb(6, 83, 173);
      }
    }
  }
  &__info {
    max-width: 22.8125rem;
    font-size: 1rem;
    line-height: 130%;
    font-weight: 700;
    span {
      color: rgb(22, 123, 242);
    }
  }
  &__img {
    position: absolute;
    top: -6.25rem;
    right: -1.5rem;
    max-width: 48.5625rem;
    img {
      width: 100%;
    }
  }
}
.info {
  background: rgb(22, 22, 22);
  padding: 2.25rem 0;
  &__items {
    display: flex;
    justify-content: space-between;
    gap: 2rem;
  }
  &__item {
    position: relative;
    max-width: 20rem;
    &-text {
      position: relative;
      font-size: 1rem;
      line-height: 130%;
      color: rgb(255, 255, 255);
      padding-left: 1.25rem;
      &:after {
        content: '';
        position: absolute;
        top: 0.5rem;
        left: 0;
        transform: rotate(-30deg);
        width: 0.625rem;
        height: 0.625rem;
        background: rgb(22, 123, 242);
      }
      span {
        font-weight: 700;
        color: rgb(22, 123, 242);
      }
    }
    &-icon {
      position: absolute;
      top: -1rem;
      left: 0.75rem;
      width: 2.625rem;
      img {
        width: 100%;
      }
    }
  }
}
.info2 {
  z-index: 2;
  padding: 7rem 0 6rem;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 3rem;
  }
  &__img {
    width: 26.9375rem;
    flex-shrink: 0;
    img {
      width: 100%;
    }
  }
  &__desc {
    width: calc(100% - 25.9375rem);
    margin-top: -2rem;
  }
  h2 {
    .text_color {
      display: block;
    }
  }
  p {
    max-width: 32rem;
    font-size: 1.5rem;
    line-height: 130%;
    font-weight: 700;
    margin: 1rem 0 0 0;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 2rem / 2));
    gap: 2rem;
    margin-top: 3.125rem;
  }
  &__item {
    &-title {
      font-size: 2.3125rem;
      line-height: 120%;
      font-weight: 700;
      color: rgb(22, 123, 242);
    }
    &-text {
      font-size: 1rem;
      line-height: 130%;
      margin-top: 0.75rem;
    }
  }
  &__btn {
    margin-top: 3.5rem;
  }
  &__img2 {
    position: absolute;
    top: 4rem;
    left: 5.625rem;
    max-width: 18.25rem;
    img {
      width: 100%;
    }
  }
  &__img3 {
    position: absolute;
    top: 13.25rem;
    right: 3.75rem;
    max-width: 17.375rem;
    img {
      width: 100%;
    }
  }
}
.services {
  padding: 1.5625rem 0 11.25rem;
  &:after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: calc(100% + 36rem);
    background-image: url("../img/services-bg.svg");
    background-repeat: no-repeat;
    background-position: top;
    background-size: cover;
    pointer-events: none;
  }
  &__top {
    p {
      font-size: 1.5rem;
      line-height: 130%;
      font-weight: 700;
      color: #ffffff;
      margin: 1rem 0 0 0;
    }
  }
  h2 {
    color: #ffffff;
    .text_color {
      display: block;
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 4rem / 3));
    gap: 2rem;
    margin-top: 3.5rem;
  }
  &__group {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2.25rem;
    margin-top: 3.5rem;
  }
  &__info {
    font-size: 1rem;
    line-height: 130%;
    font-weight: 700;
    color: #ffffff;
    span {
      color: rgb(22, 123, 242);
    }
  }
}
.service-card {
  display: flex;
  flex-direction: column;
  background-image: url("../img/service-card__bg.webp");
  background-repeat: no-repeat;
  background-position: left top;
  background-size: cover;
  &--big {
    grid-column: 1/4;
    background-image: url("../img/service-card__bg2.webp");
    .service-card__img {
      flex-grow: unset;
      padding: 0;
      margin: 0;
    }
  }
  &:hover {
    .service-card__img {
      img {
        transform: scale(1.1);
      }
    }
    .service-card__text {
      p {
        color: rgb(22, 123, 242);
      }
    }
  }
  &__images {
    display: flex;
    justify-content: center;
    gap: 4rem;
    padding: 2rem 2rem 1rem;
  }
  &__img {
    max-width: 20rem;
    flex-grow: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    margin: 0 auto;
    img {
      width: 100%;
      transition: all 0.3s;
      object-fit: contain;
    }
  }
  &__text {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 0.625rem;
    background: #ffffff;
    padding: 1.25rem 1.5rem;
    p {
      font-size: 1.25rem;
      line-height: 130%;
      margin: 0;
      strong {
        font-weight: 700;
      }
    }
    i {
      width: 1.3125rem;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      svg {
        width: 100%;
        height: auto;
      }
    }
  }
}
.advantages {
  padding: 0.625rem 0 6.25rem 0;
  h2 {
    max-width: 61.25rem;
    text-align: center;
    margin: 0 auto;
  }
  &__wrap {
    position: relative;
    width: 24.125rem;
    height: 24.125rem;
    font-family: Inter;
    margin: 6rem auto 0;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 50%;
      transform: translate(-50%, 0);
      width: 24.125rem;
      height: 24.125rem;
      border: 1.25rem solid rgba(22, 123, 242, 0.29);
      border-radius: 50%;
      pointer-events: none;
    }
    &:before {
      content: '';
      position: absolute;
      top: -2.3125rem;
      left: 50%;
      transform: translate(-50%, 0);
      width: 28.75rem;
      height: 28.75rem;
      border: 1.25rem solid rgb(235, 233, 244);
      border-radius: 50%;
      pointer-events: none;
    }
  }
  &__items {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
  }
  &__item {
    position: relative;
    width: 100%;
    display: none;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 2.375rem 2rem 3.3125rem;
    margin: 0 auto;
    &-img {
      width: 11.0625rem;
      img {
        width: 100%;
      }
    }
    &-text {
      max-width: 17.5rem;
      margin: 0.75rem auto 0;
      strong {
        font-size: 1.25rem;
        line-height: 130%;
        font-weight: 700;
      }
      p {
        font-size: 1rem;
        line-height: 130%;
        margin: 0;
      }
    }
  }
  .tabs__content--active {
    display: flex;
  }
  &__tab {
    position: absolute;
    max-width: 22.8125rem;
    width: 100%;
    display: flex;
    gap: 2rem;
    cursor: pointer;
    transition: all 0.3s;
    span {
      font-size: 1.25rem;
      line-height: 130%;
      font-weight: 700;
    }
    &:hover {
      i {
        background: rgb(134, 190, 255);
        border-color: rgb(198, 224, 255);
        img {
          transform: rotate(-45deg);
        }
      }
    }
    &:nth-child(1) {
      top: -1.875rem;
      right: calc(100% - 6.875rem);
      justify-content: flex-end;
      text-align: right;
      i {
        order: 1;
      }
    }
    &:nth-child(2) {
      top: 4.375rem;
      right: calc(100% - 0.75rem);
      justify-content: flex-end;
      text-align: right;
      i {
        order: 1;
      }
    }
    &:nth-child(3) {
      top: 13.75rem;
      right: calc(100% - -0.3125rem);
      justify-content: flex-end;
      text-align: right;
      i {
        order: 1;
      }
    }
    &:nth-child(4) {
      right: calc(100% - 6.875rem);
      bottom: -1.875rem;
      justify-content: flex-end;
      text-align: right;
      i {
        order: 1;
      }
    }
    &:nth-child(5) {
      top: -1.875rem;
      left: calc(100% - 6.875rem);
    }
    &:nth-child(6) {
      top: 4.375rem;
      left: calc(100% - 0.75rem);
    }
    &:nth-child(7) {
      top: 13.75rem;
      left: calc(100% - -0.3125rem);
    }
    &:nth-child(8) {
      left: calc(100% - 6.875rem);
      bottom: -1.875rem;
    }
    i {
      width: 2.5625rem;
      height: 2.5625rem;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      background: rgb(22, 123, 242);
      border: 0.3125rem solid rgb(172, 210, 255);
      border-radius: 50%;
      img {
        width: 1.1875rem;
        transition: all 0.3s;
      }
    }
    span {
      display: flex;
      align-items: center;
    }
  }
  .tab--active {
    i {
      background: rgb(134, 190, 255);
      border-color: rgb(198, 224, 255);
      img {
        transform: rotate(-45deg);
      }
    }
  }
  &__info {
    text-align: center;
    font-size: 1rem;
    line-height: 130%;
    margin-top: 6.25rem;
    strong {
      font-weight: 700;
    }
  }
}
.catalog {
  background: rgb(245, 245, 245);
  padding: 6rem 0;
  h2 {
    max-width: 50rem;
    text-align: center;
    margin: 0 auto;
  }
  &__subtitle {
    max-width: 50rem;
    text-align: center;
    margin: 1.25rem auto 0;
    p {
      font-size: 1.5rem;
      line-height: 130%;
      font-weight: 700;
      margin: 0;
    }
    mark {
      background: #DEE643;
    }
  }
  &__tabs {
    margin-top: 3.5rem;
  }
  &__tabs-head {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    background: rgb(22, 22, 22);
    padding: 1rem 2.5rem;
  }
  &__slider {
    display: flex;
    justify-content: center;
  }
  .slick-list {
    max-width: 43.75rem;
    margin: 0 -0.5rem;
  }
  .slick-slide {
    width: auto !important;
    margin: 0 0.5rem;
  }
  &__tab {
    font-size: 1rem;
    line-height: 130%;
    color: rgb(137, 132, 132);
    cursor: pointer;
    &:hover {
      color: #ffffff;
    }
  }
  .tab2--active {
    font-weight: 700;
    color: #ffffff;
  }
  &__prev {
    position: static;
    transform: rotate(-180deg);
    width: auto;
    height: auto;
    flex-shrink: 0;
    box-shadow: unset;
    opacity: 0;
    pointer-events: none;
    &--active {
      opacity: 1;
      pointer-events: auto;
    }
  }
  &__next {
    position: static;
    width: auto;
    height: auto;
    flex-shrink: 0;
    box-shadow: unset;
  }
  &__tabs-body {
    margin-top: 1.75rem;
  }
  &__content {
    display: none;
  }
  &__content--active {
    display: block;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 2rem / 2));
    gap: 2rem;
  }
  &__btns {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1.25rem;
    margin-top: 3.5rem;
  }
  &__img {
    position: absolute;
    top: 2.5rem;
    left: 3.75rem;
    max-width: 16.0625rem;
    img {
      width: 100%;
    }
  }
  &__img2 {
    position: absolute;
    right: 3.75rem;
    bottom: 2.5rem;
    max-width: 19.875rem;
    img {
      width: 100%;
    }
  }

}
.catalog-card {
  display: flex;
  align-items: center;
  gap: 1.5rem;
  background: rgb(255, 255, 255);
  padding: 1.5rem;
  &__img {
    width: 55%;
    img {
      width: 100%;
