}
.advantages2 {
  padding: 9.375rem 0 6rem;
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 1rem / 3));
    gap: 0.5rem;
  }
  &__group {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2.25rem;
    margin-top: 3.5rem;
  }
  &__btn {
    flex-shrink: 0;
  }
  &__info {
    font-size: 1rem;
    line-height: 130%;
    font-weight: 700;
    span {
      color: rgb(22, 123, 242);
    }
  }
}
.advantage2-card {
  position: relative;
  color: #ffffff;
  padding: 2.25rem 1.5rem;
  &:first-child {
    background: transparent !important;
    padding: 0 !important;
  }
  &:nth-child(odd) {
    background: rgb(22, 123, 242);
  }
  &:nth-child(even) {
    background: rgb(22, 22, 22);
  }
  h2 {
    position: absolute;
    left: 0;
    bottom: 0;
    max-width: 20.625rem;
    color: rgb(22, 22, 22);
    text-transform: unset;
  }
  &__title {
    font-family: Inter;
    font-size: 1.25rem;
    line-height: 130%;
    font-weight: 700;
  }
  &__text {
    font-family: Inter;
    font-size: 1rem;
    line-height: 130%;
    font-weight: 300;
    margin-top: 0.5rem;
  }
  &__number {
    position: absolute;
    right: 0;
    bottom: -1.5rem;
    font-size: 11.6875rem;
    line-height: 90%;
    font-weight: 700;
    color: rgba(255, 255, 255, 0.05);
    pointer-events: none;
  }
}
.teams {
  padding: 6rem 0;
  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  h2 {
    text-align: center;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 4.5rem / 4));
    gap: 1.5rem;
    margin-top: 3.5rem;
  }
  &__item {
    &:nth-child(2) {
      grid-column: 2/4;
      grid-row: 1/3;
    }
    &:nth-child(6) {
      grid-column: 1/3;
    }
    &:nth-child(7) {
      grid-column: 3/5;
    }
    img {
      width: 100%;
      height: 100%;
    }
  }
  &__btn {
    margin-top: 3.5rem;
  }
}
.partners {
  padding: 6rem 0 1.25rem;
  &__items {
    display: grid;
    grid-template-columns: repeat(6, calc(100% / 6 - 10rem / 6));
    gap: 2rem;
    margin-top: 2.5rem;
  }
  &__item {
    max-width: 10.3125rem;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
.feedbacks {
  padding: 0;
  &__wrap {
    position: relative;
