}
.connection {
  padding: 3.125rem 0 6rem;
  .container-fluid {
    position: relative;
    z-index: 2;
  }
  &__img {
    position: absolute;
    z-index: 1;
    top: 6.25rem;
    right: 10rem;
    max-width: 60rem;
    width: 62.6vw;
    img {
      width: 100%;
    }
  }
  &__img2 {
    position: absolute;
    top: 1.25rem;
    left: 3.75rem;
    max-width: 14.9375rem;
