.form {
  &-head {
    display: flex;
    flex-direction: column;
    font-size: 1.25rem !important;
    line-height: 130% !important;
    font-weight: 700 !important;
    text-transform: uppercase;
    .text_color {
      font-size: 1.5rem;
      color: rgb(22, 123, 242);
      margin-bottom: 0.625rem;
    }
  }
  &_block {
    display: grid;
    grid-template-columns: 100%;
    gap: 1.5rem;
    margin-top: 1.5rem;
  }
  &-group {
    max-width: 21.875rem;
    width: 100%;
  }
  .select__wrap {
    position: relative;
  }
  .select__wrap--active {
    .select__list {
      opacity: 1;
      pointer-events: auto;
    }
    .select__list-default {
      li i {
        transform: rotate(-180deg);
      }
    }
  }
  .select__list-default {
    height: 3.625rem;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgb(237, 236, 236) !important;
    border-color: transparent;
    padding: 0 1.25rem;
    font-size: 1rem;
    line-height: 130%;
    letter-spacing: 0;
    color: rgb(137, 132, 132) !important;
    li {
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      i {
        cursor: pointer;
        transition: all 0.3s;
      }
    }
  }
  .select__list {
    position: absolute;
    z-index: 3;
    top: 100%;
    left: 0;
    max-width: 100%;
    display: grid;
    grid-template-columns: 100%;
    gap: 0.75rem;
    padding: 1.5rem;
    opacity: 0;
    pointer-events: none;
    &:after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: calc(100% - 0.625rem);
      background: rgb(255, 255, 255);
      border: 0.0625rem solid rgb(137, 132, 132);
      pointer-events: none;
    }
    li {
      position: relative;
      z-index: 1;
      font-size: 1rem;
      line-height: 130%;
      color: rgb(137, 132, 132);
      padding-left: 1.5rem;
      cursor: pointer;
      &:after {
        content: '';
        position: absolute;
        top: 0.0625rem;
        left: 0;
        width: 1rem;
        height: 1rem;
        border: 0.0625rem solid rgb(22, 123, 242);
        border-radius: 50%;
      }
    }
    .li--active {
      &:after {
        background-image: url("../img/check.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
        background-color: rgb(22, 123, 242);
      }
    }
  }
  .form-checkboxes {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
