.footer {
  color: rgb(255, 255, 255);
  background: rgb(22, 22, 22);
  padding: 6rem 0;
  &__wrap {
    display: flex;
    justify-content: space-between;
    gap: 3.75rem;
  }
  &__left {
    max-width: 18.75rem;
    display: flex;
    flex-direction: column;
  }
  &__group {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-end;
    gap: 0.75rem;
    .footer__text {
      margin: 0;
    }
  }
  &__logo {
    width: 5.8125rem;
    img {
      width: 100%;
    }
  }
  &__text {
    font-size: 1rem;
    line-height: 130%;
    margin-top: 3rem;
    margin-bottom: auto;
    .text_color {
      font-weight: 700;
      color: rgb(22, 123, 242);
    }
  }
  &__group2 {
    margin-top: 3rem;
  }
  &__info {
    display: inline-flex;
    flex-direction: column;
    font-size: 0.875rem;
    line-height: 130%;
    a {
      color: rgb(22, 123, 242);
      border-bottom: 0.0625rem solid rgb(22, 123, 242);
      margin-top: 0.1875rem;
    }
  }
  &__b2b {
    max-width: 11.8125rem;
    margin-top: 0.75rem;
    img {
      width: 100%;
    }
  }
  &__items {
    display: flex;
    gap: 3.75rem;
  }
  &__item {
    max-width: 14.6875rem;
    &-group:not(:last-child) {
      margin-bottom: 1.875rem;
    }
    ul {
      display: inline-grid;
      grid-template-columns: 100%;
      gap: 0.3125rem;
      margin-top: 1rem;
    }
  }
  &__item2 {
    ul li {
      position: relative;
      padding-left: 1rem;
      &:after {
        content: '';
        position: absolute;
        top: 0.5625rem;
        left: 0;
        width: 0.1875rem;
        height: 0.1875rem;
        background: #ffffff;
        border-radius: 50%;
      }
    }
  }
  &__subtitle {
    font-size: 1rem;
    line-height: 130%;
    font-weight: 700;
    text-transform: uppercase;
    color: rgb(82, 82, 82);
  }
  &__group3 {
    display: flex;
    flex-direction: column;
    margin-top: 1.875rem;
  }
  &__phone {
    font-size: 1.25rem;
    line-height: 130%;
    font-weight: 700;
    &:hover {
      color: rgb(22, 123, 242);
    }
  }
  &__btn {
    position: relative;
    justify-content: start;
    background: transparent;
    padding-left: 1.125rem !important;
    margin-top: 0.1875rem;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(0, -50%);
      width: 0.6875rem;
      height: 0.6875rem;
      background: #ffffff;
      border: 0.1875rem solid #167BF2;
      border-radius: 50%;
    }
    &:hover {
      color: rgb(22, 123, 242);
      span {
        color: #167BF2;
        border-color: rgb(22, 123, 242);
      }
    }
    span {
      font-size: 1rem;
      line-height: 130%;
      padding: 0;
      color: #ffffff;
      border-bottom: 0.0625rem dashed rgb(22, 22, 22);
      padding-bottom: 0.125rem;
    }
  }
  &__socials {
    display: flex;
    align-items: center;
    gap: 0.875rem;
    margin-top: 1.875rem;
  }
  &__social {
    width: 2.25rem;
    height: 2.25rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgb(22, 123, 242);
    border-radius: 50%;
    padding: 0.3125rem;
    &:hover {
      background: rgb(5, 103, 218);
    }
    &:active {
      background: rgb(6, 83, 173);
    }
  }
  &__email {
    display: block;
    font-size: 1rem;
    line-height: 130%;
    margin-top: 1.875rem;
    &:hover {
      color: #167BF2;
    }
  }
  &-bottom {
    margin-top: 1.875rem;
    .footer__group2 {
      display: none;
    }
  }
  &__text2 {
    display: flex;
    flex-wrap: wrap;
    gap: 0.625rem;
    p {
      font-size: 0.875rem;
      line-height: 130%;
      margin: 0;
    }
  }
}

@media screen and (max-width: 1300px) {
  .footer {
    &__wrap {
      gap: 2rem;
    }
    &__left {
      max-width: 16.25rem;
    }
    &__text {
      font-size: 0.875rem;
    }
    &__subtitle {
      font-size: 0.875rem;
    }
    &__item {
      ul {
        li {
          font-size: 0.875rem;
        }
      }
    }
  }
}

@media screen and (max-width: 1023px) {
  .footer {
    padding: 3.75rem 0;
    &__wrap {
      flex-wrap: wrap;
      justify-content: start;
    }
    &__left {
      .footer__group2 {
        display: none;
      }
    }
    &-bottom {
      .footer__group2 {
        display: block;
        margin-top: 0;
        margin-bottom: 1.875rem;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .footer {
    &__items {
      gap: 2rem;
    }
  }
}