html {
  font-size: max(calc(16px * var(--coef_h)), 15px);
}
body {
  font-family: Ruberoid, sans-serif;
  color: rgb(22, 22, 22);
}
.fixed-body {
  overflow: hidden;
}
.wrapper {
  position: relative;
  overflow: hidden;
}
.container-fluid {
  max-width: 1350px;
}
.btn {
  display: inline-flex !important;
  justify-content: center;
  align-items: center;
  text-align: center;
  span {
    min-width: auto;
  }
}
img {
  object-fit: cover;
  display: block;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: Ruberoid;
}

@media screen and (min-width: 1921px) {
  :root {
    --zoom: calc(100vw / 1920)
  }
  body {
    zoom: var(--zoom);
  }
}
